@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Lato:wght@400;700&display=swap');

h1, h2, h3 {
  font-family: 'Playfair Display', serif;
}

/* General container */
.portfolio-container {
  min-height: 100vh;
  background-color: #fff;
  font-family: 'Playfair Display', serif;
  padding-bottom: 60px;
}

/* Banner wrapper - full bleed (escapes container) */
.portfolio-banner-wrapper {
  position: relative;
  width: 100vw;       /* Full viewport width */
  height: 100vh;      /* Full viewport height */
  left: 50%;          /* Align trick */
  right: 50%;
  margin-left: -50vw; /* Force banner to span edge-to-edge */
  margin-right: -50vw;
  overflow: hidden;
}

/* Banner video */
.portfolio-banner {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.portfolio-heading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(255, 255, 255, 0.85);
  padding: 20px 40px;
  border-radius: 12px;
  font-size: 2.2rem;
  color: #1e3a8a;
  font-weight: 700;
  letter-spacing: 1px;
  box-shadow: 0 4px 14px rgba(0,0,0,0.1);
  text-align: center;
}

/* Blog Grid Layout */
.portfolio-grid.blog-style {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 32px;
  max-width: 1100px;
  margin: 50px auto 0 auto;
  padding: 0 20px;
}

/* Blog Post Card */
.portfolio-post-card {
  background: #f9fafb;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 6px 16px rgba(0,0,0,0.06);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.portfolio-post-card h2 {
  font-size: 1.5rem;
  color: #1e3a8a;
  margin-bottom: 12px;
}

.portfolio-post-card p {
  color: #374151;
  font-size: 1rem;
  flex-grow: 1;
  margin-bottom: 16px;
}

.read-more {
  color: #2563eb;
  text-decoration: none;
  font-weight: 600;
  font-size: 0.95rem;
  transition: color 0.3s ease;
}

.read-more:hover {
  color: #1e40af;
}

/* Make entire card clickable */
.portfolio-post-card a {
  display: block;
  text-decoration: none;
  color: inherit; /* keep text colors */
  height: 100%;
  width: 100%;
}

/* Ensure inner text inherits */
.portfolio-post-card a h2,
.portfolio-post-card a p {
  color: inherit;
}

/* Keep hover effect on whole card */
.portfolio-post-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.12);
}

